.webgl-starter .options-output {
  margin: 1em 0;
}
.webgl-starter .options-output fieldset.option {
  margin: 0.3em 0 1px 0;
  padding: 0 2px 0 11px;
  border: groove 2px #DDD;
}
.webgl-starter .options-output fieldset.option > div.option > label:first-child {
  border-left: solid 10px #AAA;
  padding-left: 2px;
  margin-left: -12px;
}
.webgl-starter .options-output fieldset.option > fieldset.option {
  margin-right: calc(1px - 0.3em);
  margin-left: -10px;
  padding-left: 8px;
}
.webgl-starter .options-output fieldset.option > fieldset.option > div.option > label:first-child {
  border-left: solid 7px #AAA;
  padding-left: 2px;
  margin-left: -9px;
}
.webgl-starter .options-output fieldset.option > fieldset.option > fieldset.option {
  margin-left: -7px;
  padding-left: 5px;
}
.webgl-starter .options-output fieldset.option > fieldset.option > fieldset.option > div.option > label:first-child {
  border-left: solid 4px #AAA;
  padding-left: 2px;
  margin-left: -6px;
}
.webgl-starter .options-output fieldset.option > fieldset.option > fieldset.option > fieldset.option {
  margin-left: -4px;
  padding-left: 2px;
}
.webgl-starter .options-output fieldset.option > fieldset.option > fieldset.option > fieldset.option > div.option > label:first-child {
  border-left: solid 1px #AAA;
  padding-left: 2px;
  margin-left: -3px;
}
.webgl-starter .options-output fieldset.option > legend {
  padding: 0 2px;
  margin-left: -2px;
}
.webgl-starter .options-output fieldset.option > .buttons {
  margin: 0.3em 0;
}
.webgl-starter .options-output div.option {
  margin: 0.3em 0;
}
@media (min-width: 60em) {
  .webgl-starter .options-output div.option {
    margin-left: 15em;
  }
  .webgl-starter .options-output div.option.no-lead {
    margin-left: 0;
  }
}
@media print {
  .webgl-starter .options-output div.option.only-buttons {
    display: none;
  }
}
.webgl-starter .options-output div.option > label:first-child {
  display: block;
}
.webgl-starter .options-output div.option > label:first-child.short {
  display: inline-block;
}
@media (min-width: 60em) {
  .webgl-starter .options-output div.option > label:first-child {
    border-left: 0 !important;
    padding-left: 0 !important;
    margin-left: -15em !important;
    display: inline-block;
    width: 14.7em;
    padding-right: 0.3em;
    text-align: right;
    line-height: 0.8em;
  }
  .webgl-starter .options-output div.option > label:first-child::before {
    content: none;
  }
  .webgl-starter .options-output div.option > label:first-child + span.space {
    display: none;
  }
}
.webgl-starter .options-output div.option .nowrap {
  white-space: nowrap;
}
.webgl-starter .options-output div.option .min {
  display: inline-block;
  width: 4em;
  text-align: right;
}
.webgl-starter .options-output div.option .max {
  display: inline-block;
  width: 4em;
  text-align: left;
}
.webgl-starter .options-output div.option input[type="number"] {
  width: 4.5em;
}
.webgl-starter .options-output div.option input[type="number"][step="any"] {
  width: 12em;
}
.webgl-starter .options-output div.option input[type="text"] {
  width: 100%;
  box-sizing: border-box;
}
.webgl-starter .options-output div.option input[type="range"] {
  width: 12em;
  margin: 0;
  padding: 0;
  vertical-align: middle;
}
.webgl-starter .options-output div.option input:invalid {
  background: #F8A;
}
@media print {
  .webgl-starter .options-output button {
    display: none;
  }
}
.webgl-starter .options-output abbr {
  text-decoration: dotted underline;
}
@media print {
  .webgl-starter .options-output abbr {
    text-decoration: none;
  }
}
@media not print {
  .webgl-starter .options-output fieldset.option > .entries > .draggable-with-handle {
    position: relative;
    margin: 0.3em 1.3em;
  }
  .webgl-starter .options-output fieldset.option > .entries > .draggable-with-handle.ghost {
    opacity: .4;
  }
  .webgl-starter .options-output fieldset.option > .entries > .draggable-with-handle > [draggable],
  .webgl-starter .options-output fieldset.option > .entries > .draggable-with-handle > .delete {
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    width: 1em;
    padding: 0;
    background-color: transparent;
  }
  .webgl-starter .options-output fieldset.option > .entries > .draggable-with-handle > [draggable] > span,
  .webgl-starter .options-output fieldset.option > .entries > .draggable-with-handle > .delete > span {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
  .webgl-starter .options-output fieldset.option > .entries > .draggable-with-handle > [draggable] {
    left: -1.3em;
    cursor: move;
    background: url("data:image/gif;base64,R0lGODlhCQAJAIABADMzM////yH5BAEKAAEALAAAAAAJAAkAAAIMjH+gu+ifGIM0SlULADs=") center no-repeat, url("data:image/gif;base64,R0lGODlhCQAJAIABAIiIiP///yH5BAEKAAEALAAAAAAJAAkAAAINjI9gGowPD5uxPtpsKAA7") center;
    user-select: none;
  }
  .webgl-starter .options-output fieldset.option > .entries > .draggable-with-handle > [draggable]:hover {
    background: url("data:image/gif;base64,R0lGODlhCQAJAIABAGdcIv///yH5BAEKAAEALAAAAAAJAAkAAAIMjH+gu+ifGIM0SlULADs=") center no-repeat, url("data:image/gif;base64,R0lGODlhCQAJAIABAMKuSf///yH5BAEKAAEALAAAAAAJAAkAAAINjI9gGowPD5uxPtpsKAA7") center;
  }
  .webgl-starter .options-output fieldset.option > .entries > .draggable-with-handle > .delete {
    font-size: 1em;
    right: -1.3em;
    cursor: default;
    border: dotted 1px #888;
    box-sizing: border-box;
    background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D'http%3A//www.w3.org/2000/svg'%20width%3D'16'%20height%3D'16'%3E%3Cpath%20d%3D'M%202%202%20L%2014%2014%20M%202%2014%20L%2014%202'%20fill%3D'none'%20stroke%3D'%23444'%20stroke-width%3D'2'%20/%3E%3C/svg%3E");
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
  }
  .webgl-starter .options-output fieldset.option > .entries > .draggable-with-handle > .delete:hover {
    border-color: #D88;
    background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg%20xmlns%3D'http%3A//www.w3.org/2000/svg'%20width%3D'16'%20height%3D'16'%3E%3Cpath%20d%3D'M%202%202%20L%2014%2014%20M%202%2014%20L%2014%202'%20fill%3D'none'%20stroke%3D'%23C44'%20stroke-width%3D'2'%20/%3E%3C/svg%3E");
  }
  .webgl-starter .options-output fieldset.option > .entries > .draggable-with-handle > .delete:hover:active {
    background-position: 50% calc(50% + 1px);
  }
}
@media print {
  .webgl-starter .options-output fieldset.option > .entries > .draggable-with-handle > [draggable] {
    display: none;
  }
}
.webgl-starter details.polyfill {
  display: block;
}
.webgl-starter details.polyfill summary {
  display: block;
}
.webgl-starter details.polyfill:not([open]) > :not(summary) {
  display: none;
}
.webgl-starter details.polyfill > summary::before {
  content: "▸ ";
}
.webgl-starter details.polyfill[open] > summary::before {
  content: "▾ ";
}
.webgl-starter .code-output {
  margin: 1em 0;
}
.webgl-starter .code-output .code pre {
  border: groove 2px #DDD;
}
@media print {
  .webgl-starter .code-output .code pre code {
    white-space: pre-wrap;
    word-wrap: break-word;
  }
}
.webgl-starter .code-output .code p {
  border: groove 2px #DDD;
  color: #444;
  padding: 0.5em;
}
.webgl-starter .code-output details {
  margin: 1em 0;
}
.webgl-starter .code-output summary:hover {
  outline: solid 3px #BCF;
}
.webgl-starter .code-output input[type="number"] {
  width: 3em;
}
@media print {
  .webgl-starter .code-output button {
    display: none;
  }
  .webgl-starter .code-output a {
    text-decoration: none;
  }
  .webgl-starter .code-output details {
    display: none;
  }
  .webgl-starter .code-output details[open] {
    display: block;
  }
}
.webgl-starter .tip-info,
.webgl-starter .tip-warn {
  color: #333;
  font-family: sans-serif;
  font-size: 0.8em;
}
@media not print {
  .webgl-starter .tip-info,
  .webgl-starter .tip-warn {
    display: inline-block;
    position: relative;
    width: 1.3em;
    height: 1.3em;
    border: solid 1px black;
    border-radius: 0.65em;
    text-align: center;
    cursor: help;
  }
  .webgl-starter .tip-info .tip-content,
  .webgl-starter .tip-warn .tip-content {
    display: none;
    z-index: 1;
    text-align: left;
    cursor: default;
  }
  .webgl-starter .tip-info .tip-content kbd,
  .webgl-starter .tip-warn .tip-content kbd {
    font-size: 1.25em;
  }
  .webgl-starter .tip-info.open .tip-content,
  .webgl-starter .tip-warn.open .tip-content {
    display: block;
    position: absolute;
    top: -1px;
    left: -1px;
    padding: 0.65em;
    border: solid 1px black;
    border-radius: 0.65em;
    background: inherit;
    width: 20em;
  }
  .webgl-starter .tip-info::before,
  .webgl-starter .tip-warn::before {
    font-family: monospace;
    font-size: 1.2em;
    font-weight: bold;
  }
}
@media print {
  .webgl-starter .tip-info a,
  .webgl-starter .tip-warn a {
    text-decoration: none;
  }
}
@media not print {
  .webgl-starter .tip-info {
    background: #CEF;
  }
  .webgl-starter .tip-info::before {
    content: 'i';
  }
  .webgl-starter .tip-warn {
    background: #FF8;
  }
  .webgl-starter .tip-warn::before {
    content: '!';
  }
}
